import * as React from 'react'
import { Link, type HeadFC, type PageProps } from 'gatsby';

import { Button, Container, Image } from 'react-bootstrap';

import Footer from '../components/footer';
import LeftLargeImageCard from '../components/left-large-image-card';
import Navigation from '../components/navigation';
import ShowCase from '../components/show-case';
import ViewOnEtsy from '../components/view-on-etsy';

import hmCards1Image from '../images/handmade-cards-1.png';
import hmCardsBirthdayImg from '../images/handmade-cards-birthday.png';
import hmCardsBlankImg from '../images/handmade-cards-blank-greetings.png';
import hmCardsEmbelImg from '../images/handmade-cards-embel-greetings.png';
import hmCardsEmbosBirthdayImg from '../images/handmade-cards-embos-birthday.png';
import hmCardsEmbosNoteImg from '../images/handmade-cards-embos-note.png';
import hmCardsShowcaseImage from '../images/handmade-cards-showcase.png';

import './handmade-cards.css';

const HandmadeCardsPage: React.FC<PageProps> = () => {
  return (
    <>
      <Navigation />
      <main>
        <div className='d-flex py-5'>
          <Container className='flex-fill d-flex my-story-container'>
            <div className='my-story flex-fill'>
              <div className='row'>
                <div className='col-12 col-lg-7'>
                  <ShowCase showcaseImage={hmCardsShowcaseImage} />
                </div>
                <div className='col-12 col-lg-5 p-5'>

                  <h1>Greetings Cards</h1>
                  <div>
                    <p className='p-2'>Each card is designed and crafted by hand, bringing warmth and creativity to every message you send.</p>
                    <p className='p-2'>Whether it’s a birthday, celebration, or just a thoughtful note, our handmade cards make every occasion more personal and heartfelt.</p>
                    <div className='p-2 d-flex justify-content-between'>
                      <div className='flex-grow-1'>
                        <p>Discover new seasonal designs and custom cards available exclusively on Etsy</p>
                      </div>
                      <Link className='p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                        <Button variant="outline-dark text-nowrap">Our Store</Button>
                      </Link>
                    </div>
                  </div>

                  <Image className='py-5' src={hmCards1Image} fluid />
                </div>
              </div>
            </div>
          </Container>
        </div >
        <div className="d-flex justify-content-start flex-wrap px-2 px-md-5">
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/listing/1864013859/variety-birthday-card-bundle-elegant-fun"
              linkText="Etsy link"
              image={hmCardsBirthdayImg}
              title="Birthday Card Bundle"
              description={
                <div>
                  <p>Celebrate special days with a touch of handmade charm.</p>
                  <p>Each birthday card in this bundle is thoughtfully crafted with love, color, and creativity — making every message you send feel personal and heartfelt</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/listing/1763533251/assorted-embellished-greeting-card-set"
              linkText="Etsy link"
              image={hmCardsEmbelImg}
              title="Embellished Greeting Card Set"
              description={
                <div>
                  <p>These embellished cards feature delicate designs and textured details that make every note feel extra special.</p>
                  <p>Each card is carefully decorated by hand, adding a creative sparkle that sets it apart from store-bought cards</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/listing/1839236544/embossed-notecards-personalized-thank"
              linkText="Etsy link"
              image={hmCardsEmbosNoteImg}
              title="Embossed Notecards: Personalized Thank You Cards"
              description={
                <div>
                  <p>Show your gratitude in style with our embossed notecards.</p>
                  <p>Personalized and handmade, each card adds a thoughtful, artistic touch to your thank-you notes, making them as meaningful as your message.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/listing/1748646068/handmade-blank-greeting-cards-all"
              linkText="Etsy link"
              image={hmCardsBlankImg}
              title="Handmade Blank Greeting Cards"
              description={
                <div>
                  <p>Perfect for any occasion, these blank cards let your words shine.</p>
                  <p>Each one is uniquely handcrafted with attention to color and design, giving you a beautiful canvas for your personal message.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/listing/1864008021/handmade-embossed-happy-birthday-card"
              linkText="Etsy link"
              image={hmCardsEmbosBirthdayImg}
              title="Handmade Embossed Happy Birthday Card Set"
              description={
                <div>
                  <p>Hand-embossed and full of charm, this birthday set combines elegance with handmade artistry.</p>
                  <p>Every card is pressed and finished by hand, creating a beautiful texture that your loved ones will cherish</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
        </div>
      </main >
      <ViewOnEtsy category='Greetings Cards' />
      <Footer />
    </>
  )
}

export default HandmadeCardsPage

export const Head: HeadFC = () => <title>Cards</title>