import * as React from 'react';
import { Image } from 'react-bootstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEtsy, faFacebookF, faInstagramSquare, faPinterestP } from '@fortawesome/free-brands-svg-icons';

import { Link } from 'gatsby';

import './show-case.css';

export interface ShowCaseProps {
  showcaseImage: any;
}

function ShowCase({ showcaseImage }: ShowCaseProps) {
  return (
    <div className='d-flex flex-fill justify-content-center p-5'>
      <div className='showcase p-5'>
        <Image src={showcaseImage} fluid />

        <div className="text-center p-4">
          <Link className='col-2 p-2' to='https://www.facebook.com/love2craftllc.2024/'>
            <FontAwesomeIcon icon={faFacebookF} color="black" size="xl" />
          </Link>
          <Link className='col-2 p-2' to='https://www.pinterest.com/love2craftllc/'>
            <FontAwesomeIcon icon={faPinterestP} color="black" size="xl" />
          </Link>
          <Link className='col-2 p-2' to='https://www.instagram.com/love2craftllc/'>
            <FontAwesomeIcon icon={faInstagramSquare} color="black" size="xl" />
          </Link>
          <Link className='col-2 p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
            <FontAwesomeIcon icon={faEtsy} color="black" size="xl" />
          </Link>
        </div>
      </div>
    </div>
  )
}

export default ShowCase